// app/projects/[id]/page.tsx
"use client"

import * as React from "react"
import { useParams, useRouter } from "next/navigation"
import { useProject } from "@/hooks/use-projects"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Progress } from "@/components/ui/progress"
import { Skeleton } from "@/components/ui/skeleton"
import { 
  ArrowLeft, 
  Building, 
  Calendar, 
  Monitor, 
  Smartphone, 
  Globe, 
  Megaphone,
  CheckCircle2,
  AlertCircle,
  Edit,
  FileText,
  BarChart3
} from "lucide-react"
import Link from "next/link"

const getProjectIcon = (type: string) => {
  switch (type) {
    case "Web App": return <Monitor className="h-5 w-5" />
    case "Mobile App": return <Smartphone className="h-5 w-5" />
    case "Website": return <Globe className="h-5 w-5" />
    case "Social Media Campaign": return <Megaphone className="h-5 w-5" />
    default: return <Monitor className="h-5 w-5" />
  }
}

const getTypeColor = (type: string) => {
  switch (type) {
    case "Web App": return "bg-blue-500/10 text-blue-500 border-blue-500/20"
    case "Mobile App": return "bg-purple-500/10 text-purple-500 border-purple-500/20"
    case "Website": return "bg-green-500/10 text-green-500 border-green-500/20"
    case "Social Media Campaign": return "bg-pink-500/10 text-pink-500 border-pink-500/20"
    default: return "bg-blue-500/10 text-blue-500 border-blue-500/20"
  }
}

const translateType = (type: string) => {
  switch (type) {
    case "Web App": return "Application Web"
    case "Mobile App": return "Application Mobile"
    case "Website": return "Site Web"
    case "Social Media Campaign": return "Campagne Réseaux Sociaux"
    default: return type
  }
}

const translateStatus = (status: string) => {
  return status === "Open" ? "Ouvert" : "Terminé"
}

export default function ProjectDetailsPage() {
  const params = useParams()
  const router = useRouter()
  const projectId = params.id as string
  
  const { data: project, isLoading, error } = useProject(projectId)

  if (isLoading) {
    return (
      <div className="space-y-6">
        <div className="flex items-center gap-4">
          <Skeleton className="h-10 w-10" />
          <div className="flex-1">
            <Skeleton className="h-8 w-64 mb-2" />
            <Skeleton className="h-4 w-96" />
          </div>
        </div>
        <div className="grid gap-6 md:grid-cols-2">
          <Skeleton className="h-64" />
          <Skeleton className="h-64" />
        </div>
      </div>
    )
  }

  if (error || !project) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="text-center">
          <AlertCircle className="h-12 w-12 text-destructive mx-auto mb-4" />
          <h2 className="text-2xl font-semibold mb-2">Projet introuvable</h2>
          <p className="text-muted-foreground mb-4">
            Le projet que vous recherchez n'existe pas ou a été supprimé.
          </p>
          <Button asChild>
            <Link href="/projects">
              <ArrowLeft className="mr-2 h-4 w-4" />
              Retour aux projets
            </Link>
          </Button>
        </div>
      </div>
    )
  }

  return (
    <div className="space-y-6">
      {/* Breadcrumb */}
      <div className="flex items-center gap-2 text-sm text-muted-foreground">
        <Link href="/projects" className="hover:text-foreground transition-colors">
          Projets
        </Link>
        <span>/</span>
        <span className="text-foreground">{project.name}</span>
      </div>

      {/* Header */}
      <div className="flex items-start justify-between gap-4">
        <div className="flex items-start gap-4">
          <Button variant="outline" size="icon" onClick={() => router.back()}>
            <ArrowLeft className="h-4 w-4" />
          </Button>
          <div>
            <h1 className="text-3xl font-semibold tracking-tight text-foreground mb-2">
              {project.name}
            </h1>
            <div className="flex items-center gap-2 flex-wrap">
              <Badge variant="outline" className={`${getTypeColor(project.type)} flex items-center gap-1`}>
                {getProjectIcon(project.type)}
                {translateType(project.type)}
              </Badge>
              <Badge
                variant={project.status === "Open" ? "default" : "secondary"}
                className={
                  project.status === "Open"
                    ? "bg-green-500/10 text-green-500 border-green-500/20"
                    : "bg-muted text-muted-foreground"
                }
              >
                {translateStatus(project.status)}
              </Badge>
            </div>
          </div>
        </div>
        <Button disabled={project.status === "Completed"}>
          <Edit className="mr-2 h-4 w-4" />
          Modifier
        </Button>
      </div>

      {/* Cards Grid */}
      <div className="grid gap-6 md:grid-cols-2">
        {/* Informations Générales */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <FileText className="h-5 w-5" />
              Informations Générales
            </CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <div className="grid gap-3">
              <div>
                <p className="text-sm text-muted-foreground">Client</p>
                <p className="text-base font-medium flex items-center gap-2">
                  <Building className="h-4 w-4 text-muted-foreground" />
                  {project.client || "Non spécifié"}
                </p>
              </div>
              <div>
                <p className="text-sm text-muted-foreground">Date de début</p>
                <p className="text-base font-medium flex items-center gap-2">
                  <Calendar className="h-4 w-4 text-muted-foreground" />
                  {new Date(project.startDate).toLocaleDateString('fr-FR', {
                    day: 'numeric',
                    month: 'long',
                    year: 'numeric'
                  })}
                </p>
              </div>
              <div>
                <p className="text-sm text-muted-foreground">Statut de l'analyse</p>
                <div className="flex items-center gap-2 mt-1">
                  {project.analysisComplete ? (
                    <>
                      <CheckCircle2 className="h-4 w-4 text-green-500" />
                      <span className="text-base font-medium text-green-500">Complétée</span>
                    </>
                  ) : (
                    <>
                      <AlertCircle className="h-4 w-4 text-amber-500" />
                      <span className="text-base font-medium text-amber-500">En cours</span>
                    </>
                  )}
                </div>
              </div>
              <div>
                <p className="text-sm text-muted-foreground">Nombre de rapports</p>
                <p className="text-2xl font-bold">{project.reportsCount}</p>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Progression */}
        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <BarChart3 className="h-5 w-5" />
              Progression du Projet
            </CardTitle>
          </CardHeader>
          <CardContent className="space-y-6">
            <div className="space-y-2">
              <div className="flex items-center justify-between">
                <span className="text-sm text-muted-foreground">Avancement global</span>
                <span className="text-2xl font-bold">{project.progress}%</span>
              </div>
              <Progress value={project.progress} className="h-3" />
            </div>
            <div className="grid gap-3 text-sm">
              <div className="flex items-center justify-between p-3 rounded-lg bg-muted/50">
                <span className="text-muted-foreground">Créé le</span>
                <span className="font-medium">
                  {new Date(project.createdAt).toLocaleDateString('fr-FR')}
                </span>
              </div>
              <div className="flex items-center justify-between p-3 rounded-lg bg-muted/50">
                <span className="text-muted-foreground">Dernière mise à jour</span>
                <span className="font-medium">
                  {new Date(project.updatedAt).toLocaleDateString('fr-FR')}
                </span>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Actions rapides */}
      <Card>
        <CardHeader>
          <CardTitle>Actions Rapides</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="flex gap-3 flex-wrap">
            <Button variant="outline" disabled>
              <FileText className="mr-2 h-4 w-4" />
              Voir les rapports
            </Button>
            <Button variant="outline" disabled>
              <BarChart3 className="mr-2 h-4 w-4" />
              Analyses détaillées
            </Button>
          </div>
        </CardContent>
      </Card>
    </div>
  )
}